<template>
    <div class='accout'>
        <!--头部-->
        <div>
            <CommonHeader></CommonHeader>
        </div>
        <!--左边导航部分-->
        <div class='fix'>
            <Set></Set>
        </div>
        <!--收支明细内容区域-->

        <div class='IncomeDetails'>
            <div class='I-top'>
                <span class='I-t-Leftborder'></span>
                <span class='I-t-text'>收支明细</span>
                <button type="button" class="btn btn-primary" @click='backWithdrawals' style="font-family:simsun">&lt;&lt;返回我的钱包</button>
            </div>
            <div class='I-bottom'>
                <div class='I-bottom-top'>
                    <span class="demonstration">时间选择：</span>
                    <el-date-picker class='I-b-inputBox1' v-model="timeFrame.startDate" type="date" @change='dataOne' placeholder="选择日期" :picker-options="pickerOptions0">
                    </el-date-picker>
                    <span class="demonstration">-</span>
                    <el-date-picker class='I-b-inputBox2' v-model="timeFrame.endDate" type="date" @change='dataTwo' placeholder="选择日期" :picker-options="pickerOptions0">
                    </el-date-picker>
                    <div class="btn-group" role="group">
                        <!--<button type="button" class='btn oneBtn' :class="{'btn-primary':oneBlue,'btn-default':oneWhite}" @click='oneBtn'>最近1个月</button>
                                                                        <button type="button" class='btn twoBtn' :class="{'btn-primary':twoBlue,'btn-default':twoWhite}" @click='twoBtn'>最近2个月</button>
                                                                        <button type="button" class='btn threeBtn' :class="{'btn-primary':threeBlue,'btn-default':threeWhite}" @click='threeBtn'>最近3个月</button>-->
                        <el-select v-model="dateChoose" placeholder="日期范围" @change='dateChange' style='width:120px;margin-left:8px;'>
                            <el-option value="1" label="近7天"></el-option>
                            <el-option value="2" label="近一个月"></el-option>
                            <el-option value="3" label="近两个月"></el-option>
                            <el-option value="4" label="近三个月"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class='I-bottom-bottom'>
                    <table class='table table-bordered'>
                        <thead>
                            <tr>
                                <th>时间</th>
                                <th>名称</th>
                                <th>来源/去处</th>
                                <th>金额（元）</th>
                                <th>状态</th>
                                <th>余额</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for='item in moneyMes'>
                                <td>{{item.updatedWhen||item.createdWhen | dataTimeFilter}}</td>
                                <td>{{typeFilter(item.type)}}</td>
                                <td>{{item.description}}</td>
                                <td>{{typeMoneyFilter(item.money,item.type)}}</td>
                                <td>{{item.status | moneyMes}}</td>
                                <td>{{item.balance}}</td>
                            </tr>
                            <tr v-if='noMesShow'>
                                <td colspan="6">
                                    暂无数据
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <!--翻页组件-->
                    <flip @customEvent='customEvent' :Body='moneyBody' :Condition='params'></flip>
                    <!--翻页组件-->
                </div>
            </div>
        </div>
    </div>
</template>
<script>
//头部
import CommonHeader from '../../public/header.vue';
//tab导航
import Set from '../../public/set.vue';
import $ from 'jquery'
// 日期控件
import '../../../../static/css/daterangepicker.css'
import '../../../../static/js/daterangepicker.js'
import moment from 'moment'
import flip from '../../public/flip.vue'
export default {
    created() {
        // 默认日期设置
        this.defaultDate()
        // 查询收支明细
        this.checkMoneyMes();


    },
    data() {
        return {
            // 日期范围的六个按钮
            oneBlue: false,
            oneWhite: true,
            twoBlue: false,
            twoWhite: true,
            threeBlue: false,
            threeWhite: true,
            timeFrame: {
                startDate: '',
                endDate: ''
            },
            value1: '',
            pickerOptions0: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },

            },
            Authorization: '',
            moneyMes: [],
            pageTotal: 1,
            params: {
                startTime: '',
                endTime: '',
                pageNumber: 1,
                pageSize: 10
            },
            moneyBody: {},
            noMesShow: false,//没有数据
            dateChoose: '2'
        }
    },
    methods: { // 日期选择        
        dateChange() {
            if (this.dateChoose == 1) {
                //    近7天的日期显示
                var endDate = new Date();
                var startDate = endDate - 1000 * 60 * 60 * 24 * 7;
                endDate = moment(endDate).format('YYYY-MM-DD')
                startDate = moment(startDate).format('YYYY-MM-DD');
                this.timeFrame.endDate = endDate
                this.timeFrame.startDate = startDate
                this.params.startTime = startDate
                this.params.endTime = endDate
                this.params.pageNumber = 1
                this.checkMoneyMes()
            } else if (this.dateChoose == 2) {
                //    近一个月的日期显示
                var endDate = new Date();
                var startDate = endDate - 1000 * 60 * 60 * 24 * 30;
                endDate = moment(endDate).format('YYYY-MM-DD')
                startDate = moment(startDate).format('YYYY-MM-DD');
                this.timeFrame.endDate = endDate
                this.timeFrame.startDate = startDate
                this.params.startTime = startDate
                this.params.endTime = endDate
                this.params.pageNumber = 1
                this.checkMoneyMes()
            } else if (this.dateChoose == 3) {
                //    近两个月的日期显示
                var endDate = new Date();
                var startDate = endDate - 1000 * 60 * 60 * 24 * 60;
                endDate = moment(endDate).format('YYYY-MM-DD')
                startDate = moment(startDate).format('YYYY-MM-DD')
                this.timeFrame.endDate = endDate
                this.timeFrame.startDate = startDate
                this.params.startTime = startDate
                this.params.endTime = endDate
                this.params.pageNumber = 1
                this.checkMoneyMes()
            } else if (this.dateChoose == 4) {
                //    近三个月的日期显示
                var endDate = new Date();
                var startDate = endDate - 1000 * 60 * 60 * 24 * 90;
                endDate = moment(endDate).format('YYYY-MM-DD')
                startDate = moment(startDate).format('YYYY-MM-DD')
                this.timeFrame.endDate = endDate
                this.timeFrame.startDate = startDate
                this.params.startTime = startDate
                this.params.endTime = endDate
                this.params.pageNumber = 1
                this.checkMoneyMes()
            }
        },
        typeFilter(type) {
            if (type == 1) {
                return '收入'
            }
            if (type == 2) {
                return '提现'
            }
            if (type == 4) {
                return '退款'
            }
        },
        typeMoneyFilter(money, type) {
            if (type == 1) {
                return '+' + money
            }
            if (type == 2) {
                return '-' + money
            }
            if (type == 4) {
                return '+' + money
            }
        },
        warning1() {
            this.$message({
                message: '已经是最后一页',
                type: 'warning'
            });
        },
        warning2() {
            this.$message({
                message: '已经是第一页',
                type: 'warning'
            });
        },
        // 返回我的钱包
        backWithdrawals() {
            this.$router.push({ name: 'Account' })
        },
        //    查看收支明细
        checkMoneyMes() {
            this.Authorization = localStorage.getItem('access_token');
            var url = '/api/assist-order/orderDetail/details'
            if (this.params.startTime) { this.params.startTime = this.timeFrame.startDate + ' 00:00:00' }
            if (this.params.endTime) { this.params.endTime = this.timeFrame.endDate + ' 23:59:59' }
            this.$http.get(url, {
                headers: {
                    Authorization: 'bearer ' + this.Authorization
                },
                params: this.params
            }).then(function(res) {
                this.moneyMes = res.body.list;
                if (this.moneyMes.length == 0) {
                    this.noMesShow = true
                } else if (this.moneyMes.length > 0) {
                    this.noMesShow = false
                }
                this.moneyBody = res.body
                this.pageTotal = res.body.lastPage
            })

        },
        // 翻页组件事件
        customEvent(params) {
            this.params = params
            this.checkMoneyMes();
        },
        // 默认的日期设置
        defaultDate() {
            let today = new Date().getTime()
            let oneMonthBefore = today - 1000 * 60 * 60 * 24 * 30;
            let endDate = moment(today).format('YYYY-MM-DD')
            let startDate = moment(oneMonthBefore).format('YYYY-MM-DD')
            this.timeFrame.startDate = startDate
            this.timeFrame.endDate = endDate
            this.params.startTime=startDate
            this.params.endTime=endDate            
        },
        // 解决前大后
        dataOne() {
            if (new Date(this.timeFrame.startDate) >= new Date(this.timeFrame.endDate)) {
                var t = new Date(this.timeFrame.startDate) + 1000 * 60 * 60 * 24
                this.timeFrame.endDate = moment(t).format('YYYY-MM-DD')
                this.params.endTime = this.timeFrame.endDate
            }
            this.timeFrame.startDate = moment(this.timeFrame.startDate).format('YYYY-MM-DD')
            this.params.startTime = this.timeFrame.startDate
            this.params.pageNumber = 1
            this.checkMoneyMes()
        },
        // 解决前大后        
        dataTwo() {
            if (new Date(this.timeFrame.startDate) >= new Date(this.timeFrame.endDate)) {
                var t = new Date(this.timeFrame.startDate) + 1000 * 60 * 60 * 24
                this.timeFrame.endDate = moment(t).format('YYYY-MM-DD')
                this.params.endTime = this.timeFrame.endDate
            }
            this.timeFrame.endDate = moment(this.timeFrame.endDate).format('YYYY-MM-DD')
            this.params.endTime = this.timeFrame.endDate
            this.params.pageNumber = 1
            this.checkMoneyMes()
        },

    },
    components: {
        CommonHeader: CommonHeader,
        Set: Set,
        flip: flip
    }
}

</script>
<style scoped>
/*大内容块样式*/

.IncomeDetails {
    margin: 50px 20px 0px 195px;
    min-width: 960px;
    background: white;
    font-size: 14px;
}














/*第四个界面头部样式*/

.IncomeDetails .I-top {
    height: 50px;
    padding-top: 8px;
    padding-bottom: 8px;
    border-bottom: solid 1px #DDD;
    position: relative;
}

.IncomeDetails .I-top .I-t-Leftborder {
    display: block;
    width: 1px;
    height: 14px;
    background: #6886d4;
    position: absolute;
    left: 1px;
    top: 16px;
}

.I-t-text {
    padding: 8px;
}














/*第四个界面底部样式*/

.IncomeDetails .I-bottom {
    padding: 15px;
}

.IncomeDetails .I-bottom .I-bottom-top {
    margin-bottom: 15px;
}

.I-b-inputBox1 .el-input__inner {
    border: 1px solid red;
    font-size: 30px;
}














/*下部分表格样式*/

.I-bottom table th,
.I-bottom table {
    text-align: center;
}














/*table样式*/

.I-bottom .table thead {
    background: #f5f5f5;
}

.I-bottom .table tr {
    text-align: center;
}

.I-bottom .table td {
    padding: 4px 0px;
    line-height: 39px;
}

.I-bottom .table th {
    text-align: center;
    border-bottom: none;
}

.I-bottom .flipBtn {
    text-align: center;
}
</style>



